.detail-div {
    width: 100%;
    padding-top: 15vmin;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.detail-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    max-width: 800px;
    gap: 1rem;
}

.detail-img-container {
    width: 100%;
    height: 400px;
}

.product-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.detail-description {
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.detail-item-name-price {
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 1fr;
    min-width: 0;
}

.detail-item {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.product-color-ul {
    display: flex;
    gap: 1rem;
}

.product-color {
    border: 1px solid black;
    width: 35px;
    height: 35px;
    border-radius: 100%;
}

.color-name {
    width: 100%;
    display: flex;
    justify-content: space-between;
    gap: .5rem;
    align-items: center;
}

.color-name-ul {
    display: flex;
    gap: .5rem;
    align-items: center;
}

.product-size-ul {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(35px, 1fr));
    gap: 1rem;
    align-items: center;
}

.product-size {
    background-color: RGB(220, 220, 220);
    padding: .5rem;
    border-radius: var(--small-border-radius);
    display: flex;
    justify-content: center;
}

.add-to-bag-btn {
    width: 100%;
    color: white;
    background-color: black;
    border: none;
    outline: none;
    padding: .5rem;
    border-radius: var(--small-border-radius);
    max-width: 400px;
}

.product-description {
    /* line-height: 3vmin; */
}

.policy-container {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: 100%;
    grid-template-rows: repeat(3, 1fr);
    gap: 1rem;
    min-width: 0;
    min-height: 0;
}

.policy-item {
    gap: .5rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.policy-icon-container {
    width: 40px;
    height: 40px;
}

.policy-icon {
    width: 100%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.loading-div {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}